.hero {
  background-image: url(../../images/hero.webp);
  min-height: 524px;
  display: flex;
  align-items: center;
  background-size: cover;
}
.hero__content {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 38px 24px;
  text-align: center;
  color: #a9a9a9;
}

.hero__heading {
  font-size: 2.5rem;
}

.hero__subtext {
  font-weight: 300;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 1);
}

.hero-text--highlighted {
  font-size: 1.2em;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}

.hero .btn {
  align-self: center;
}

@media (min-width: 576px) {
  .hero__heading {
    font-size: 2.75rem;
  }
}

@media (min-width: 768px) {
  .hero__heading {
    font-size: 2.85rem;
  }
}

@media (min-width: 992px) {
  .hero {
    min-height: 100vh;
    background-size: cover;
  }

  .hero__heading {
    font-size: 3.25rem;
  }

  .hero__subtext {
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 1);
  }
}

@media (min-width: 1200px) {
  .hero__heading {
    font-size: 3.5rem;
  }
  .hero__content {
    padding-right: 45%;
    padding-left: 0;
  }
}

@media (min-width: 1800px) {
  .hero__content {
    padding-right: 40%;
    padding-left: 0;
  }
}

